            </el-col>
          </el-row>
          <el-row v-for="(item, index) in tableList" :key="item.addr" class="sale-list__item">
            <el-col :span="2" class="text-center">
              <div class="text-white text-18 break-word">{{ index + 1 }}</div>
            </el-col>
            <el-col :span="22" class="text-center">
              <div class="flex-row-center sale-list__content">
                <div class="text-white text-18 break-word">{{ item.addr }}</div>
                <el-icon v-if="item.info.amount.toString() != '0'" color="#52C41A" class="ML-10"><icon-select /></el-icon>
              </div>
            </el-col>
          </el-row>
          <el-pagination
            v-model:current-page="currentPage"
            class="MT-10"
            background
            layout="prev, pager, next"
            :total="total"
            :pager-count="5"
            :page-size="limit"
            @current-change="pageChange"
          />
        </div>
      </el-col>
      <el-col :xs="24" :sm="24" :md="24" :lg="12" :xl="12">
        <div v-loading="buyLoading || userLoading" class="border-box progress" element-loading-background="rgba(0, 0, 0, 0.8)">
          <el-row>
            <el-col :xs="20" :sm="18" :md="18" :lg="16" :xl="16" class="progress-col">
              <div class="progress-title text-16">Make sure the website is ScoutDoge.Com</div>
            </el-col>
          </el-row>
          <div v-if="getStatus != 'ended' && childStatus != 'available'" class="progress-time flex-row-center MT-20 MB-20">
            <div class="progress-time__item text-20 text-bold MR-20">{{ countDownObj.days }}</div>
            <div class="progress-time__item text-20 text-bold MR-20">{{ countDownObj.hours }}</div>
            <div class="progress-time__item text-20 text-bold MR-20">{{ countDownObj.minutes }}</div>
            <div class="progress-time__item text-20 text-bold">{{ countDownObj.seconds }}</div>
          </div>
          <el-progress class="MT-20 MB-20" type="circle" :percentage="(soldETH / totalMax) * 100" :width="144" :stroke-width="12" :format="format" color="#F5BD02" />

          <div class="text-18 text-white">
            <span class="progress-text__active">{{ soldETH }}BNB/</span>
            <span>{{ totalMax }}BNB</span>
          </div>
          <div v-if="soldETH >= totalMax || getStatus == 'ended'" class="text-fff text-16 MT-20">This pool has ended</div>
          <p v-if="childStatus == 'available'" class="text-16 text-white MT-20">You will received: {{ tokensNum }} SDOGE</p>
          <div v-if="getStatus != 'incoming' && getStatus != 'ended' && childStatus != 'available'" class="text-white text-18 MT-30">Amount(MAX：{{ userBuyMax }}BNB)</div>
          <template v-if="isLogin">
            <template v-if="getStatus != 'ended' && getStatus != 'incoming' && childStatus != 'waitReceived' && childStatus != 'available' && isAllow">
              <div class="flex-row-center MT-15">
                <div class="progress-input flex-row-center">
                  <input v-model="inputMax" type="text" class="text-18 flex-1 text-white" placeholder="0.0">
                  <span class="text-18 progress-input__max pointer" @click="getMax">MAX</span>
                </div>
                <div class="progress-buy text-18 ML-20 pointer" @click="buyHandler">BUY</div>
              </div>
              <p class="text-16 text-white MT-20">You will received: {{ inputMax * (originData.ETHPrice || 0) }} SDOGE</p>
            </template>
            <div v-if="childStatus == 'available'" class="flex-row-center MT-15">
              <div class="progress-buy text-18 pointer" @click="claimTokens">CLAIM TOKENS</div>
            </div>
          </template>
          <div v-else class="flex-row-center MT-15">
            <div class="progress-buy text-18 pointer" @click="loginHandler">Connect</div>
          </div>
          <p v-if="isLogin && !isAllow" class="text-20 text-red MT-10">You are not in whitelist</p>
        </div>
        <div class="border-box sale-list">
          <el-row class="sale-list__item">
            <el-col :span="12" class="text-left">
              <div class="text-white text-18 break-word">Status</div>
            </el-col>
            <el-col :span="12" class="text-right">
              <div class="text-white text-18 break-word">{{ getStatus }}</div>
            </el-col>
          </el-row>
          <el-row class="sale-list__item">
            <el-col :span="12" class="text-left">
              <div class="text-white text-18 break-word">Unsold token</div>
            </el-col>
            <el-col :span="12" class="text-right">
              <div class="text-white text-18 break-word">Scout Doge</div>
            </el-col>
          </el-row>
          <el-row class="sale-list__item">
            <el-col :span="12" class="text-left">
              <div class="text-white text-18 break-word">Sale type</div>
            </el-col>
            <el-col :span="12" class="text-right">
              <div class="text-white text-18 break-word">Whitelist Only</div>
            </el-col>
          </el-row>
          <el-row class="sale-list__item">
            <el-col :span="12" class="text-left">
              <div class="text-white text-18 break-word">Minimum Buy</div>
            </el-col>
            <el-col :span="12" class="text-right">
              <div class="text-white text-18 break-word">{{ minimum }}BNB</div>
            </el-col>
          </el-row>
          <el-row class="sale-list__item">
            <el-col :span="12" class="text-left">
              <div class="text-white text-18 break-word">Maximum Buy</div>
            </el-col>
            <el-col :span="12" class="text-right">
              <div class="text-white text-18 break-word">{{ maximum }}BNB</div>
            </el-col>
          </el-row>
          <el-row class="sale-list__item">
            <el-col :span="12" class="text-left">
              <div class="text-white text-18 break-word">Total Contributors</div>
            </el-col>
            <el-col :span="12" class="text-right">
              <div class="text-white text-18 break-word">{{ totalCon }}</div>
            </el-col>
          </el-row>
          <el-row class="sale-list__item">
            <el-col :span="12" class="text-left">
              <div class="text-white text-18 break-word">Your Purchased</div>
            </el-col>
            <el-col :span="12" class="text-right">
              <div class="text-white text-18 break-word">{{ amount }}BNB</div>
            </el-col>
          </el-row>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { Select } from '@element-plus/icons'
import { utils } from 'ethers'
import { ElNotification } from 'element-plus'
import moment from 'moment'
export default {
  components: {
    IconSelect: Select
  },
  data() {
    return {
      dataList: [
        {
          label: 'Token Name',
          value: 'ScoutDoge'
        },
        {
          label: 'Token Symbol',
          value: 'SDOGE'
        },
        {
          label: 'Token Decimals',
          value: '9'
        },
        {
          label: 'Token Address ',
          value: '0x9E551b430B7Ab145C96c6eDe72a34eC2A90d4eca',
          desc: '(Do not send BNB to the token address!)',
          type: 'link'
        },
        {
          label: 'Total Supply',
          value: '1,000,000,000,000 SDOGE'
        },
        {
          label: 'Tokens For Presale',
          value: '50,000,000,000 SDOGE'
        },
        {
          label: 'Tokens For Liquidity',
          value: '32,000,000,000 SDOGE'
        },
        {
          label: 'Presale Rate',
          value: '--'
        },
        {
          label: 'Listing Rate',
          value: '1 BNB = 80,000,000 SDOGE'
        },
        {
          label: 'Soft Cap',
          value: '--'
        },
        {
          label: 'Hard Cap',
          value: '--'
        },
        {
          label: 'Unsold Tokens',
          value: 'Burn'
        },
        {
          label: 'Presale Start Time',
          value: '--'
        },
        {
          label: 'Presale End Time',
          value: '--'
        },
        {
          label: 'Liquidity Lockup Time',
          value: '1825 days after pool ends'
        }
      ],
      tableList: [],
      loading: false,
      // 原始数据
      originData: {},
      // 用户信息
      userData: {},
      // 输入框数量
      inputMax: 0,
      // 用户还可以购买的最大数量
      userBuyMax: 0,
      buyLoading: false,
      // 刷新state定时器
      stateTimer: null,
      // 倒计时定时器
      countDownTimer: null,
      // 倒计时
      countDownObj: {
        days: '00',
        hours: '00',
        minutes: '00',
        seconds: '00'
      },
      // 从哪里开始
      from: 0,
      // 每页多少条
      limit: 10,
      // 总条数
      total: 0,
      // 加载白名单
      pageLoading: false,
      // 总贡献人数
      totalCon: 0,
      // 当前用户是否登陆
      isLogin: false,
      userLoading: false,
      currentPage: 1,
      // 已购买的数量
      amount: 0,
      // 子状态
      childStatus: '',
      // 可领取的币
      tokensNum: 0,
      // 是否有领取过
      isReceived: true,
      // 是否在白名单
      isAllow: false
    }
  },
  computed: {
    // 最小购买值
    minimum() {
      return utils.formatEther(this.originData?.purchaseMin || 0)
    },
    // 最大购买值
    maximum() {
      return utils.formatEther(this.originData?.purchaseMax || 0)
    },
    // 总数量
    totalMax() {
      return utils.formatEther(this.originData?.totalMax || 0)
    },
    // 目前购买数量
    soldETH() {
      return utils.formatEther(this.originData?.SoldETH || 0)
    },
    // 获取当前状态
    getStatus() {
      const currentTime = moment().unix()
      const { startTime, stopTime, receivedTime, enableReceived } = this.originData
      // 如果当前时间小于开始时间，则还未开售
      if (currentTime < startTime) {
        return 'incoming'
        // 当前时间在开始时间和结束时间之内，则属于进行中
      } else if (currentTime >= startTime && currentTime <= stopTime) {
        return 'inprogress'
        // 否则已结束
      } else {
        // 当前时间小于领取时间
        if (currentTime <= receivedTime) {
          // eslint-disable-next-line vue/no-side-effects-in-computed-properties
          this.childStatus = 'waitReceived'
          // 当前时间大于可领取时间，并且可以领取
        } else if (enableReceived) {
          if (this.$wallet.preSaleABI) {
            this.getTokensNum()
          }
          // eslint-disable-next-line vue/no-side-effects-in-computed-properties
          this.childStatus = 'available'
        }
        return 'ended'
      }
    }
  },
  watch: {
    inputMax(val) {
      if (isNaN(val)) {
        this.inputMax = this.minimum
      }
      val = Number(val)
      const max = this.userBuyMax || this.maximum
      // 目前还剩多少可以购买
      const res = this.totalMax - this.soldETH
      // 当前值小于可以购买的值
      if (val < res) {
        if (val < this.minimum) {
          this.inputMax = this.minimum
        } else if (val > max) {
          this.inputMax = max
        }
      } else if (val > res) {
        this.inputMax = res
      }
    }
  },
  mounted() {
    this.initData()
    this.getUserInfo()
    // 5分钟刷新一次State
    this.stateTimer = setInterval(() => {
      this.initData(false)
    }, 1000 * 20)
    this.$wallet.on('accountChange', this.getUserInfo)
  },
  beforeDestroy() {
    clearInterval(this.stateTimer)
    clearInterval(this.countDownTimer)
  },
  methods: {
    format() {
      return 'BNB'
    },
    // 认领代币
    async claimTokens() {
      this.buyLoading = true
      this.$wallet.preSaleABI
        .receivedToken()
        .then((data) => {
          this.getTokensNum()
        })
        .catch((error) => {
          ElNotification({
            title: 'Error',
            message: error.data ? error.data.message || error.message : error.message,
            type: 'error'
          })
        })
        .finally(() => {
          this.buyLoading = false
        })
    },
    // 获取能够领取的币的数量
    getTokensNum() {
      this.$wallet.preSaleABI.canReceivedToken().then((data) => {
        this.tokensNum = utils.formatUnits(data, 9)
      })
    },
    async loginHandler() {
      this.buyLoading = true
      try {
        await this.$wallet.initNetwork()
        this.buyLoading = false
      } catch (error) {
        this.buyLoading = false
      }
    },
    // 跳转分页
    async pageChange(page, refresh = true) {
      this.pageLoading = refresh
      try {
        const whiteList = await this.$wallet.preSaleABI.getAllowList(this.limit * (page - 1), this.limit)
        this.tableList = whiteList
        this.pageLoading = false
      } catch (error) {
        this.pageLoading = false
      }
    },
    /**
     * 获取页面数据
     * @param {Boolean} refresh 是否需要刷新
     */
    async initData(refresh = true) {
      this.loading = refresh
      try {
        const data = await this.$wallet.preSaleABI.State()
        const totalWhiteCount = await this.$wallet.preSaleABI.getAllowSize()
        this.$wallet.preSaleABI.getSoldSize().then((data) => {
          this.totalCon = data.toNumber()
        })
        await this.pageChange(this.currentPage, false)
        this.total = Number(totalWhiteCount)
        this.inputMax = this.minimum
        this.originData = data
        const { startTime, stopTime, totalMin, totalMax, ETHPrice } = data
        const startTime1 = moment(startTime * 1000).format('YYYY.MM.DD HH:mm')
        const endTime = moment(stopTime * 1000).format('YYYY.MM.DD HH:mm')
        this.dataList[7].value = `1 BNB = ${ETHPrice}SDOGE`
        this.dataList[9].value = `${utils.formatEther(totalMin)}BNB`
        this.dataList[10].value = `${utils.formatEther(totalMax)}BNB`
        this.dataList[12].value = `${startTime1}（UTC）`
        this.dataList[13].value = `${endTime}（UTC）`
        this.loading = false
        this.countDown()
      } catch (error) {
        this.loading = false
        ElNotification({
          title: error.code,
          message: error.data ? error.data.message || error.message : error.message,
          type: 'error'
        })
      }
    },
    // 倒计时
    countDown() {
      clearInterval(this.countDownTimer)
      const { startTime, stopTime, receivedTime } = this.originData
      let end
      if (this.getStatus === 'incoming') {
        end = moment(startTime * 1000)
      } else if (this.getStatus === 'inprogress') {
        end = moment(stopTime * 1000)
      } else if (this.childStatus === 'waitReceived') {
        end = moment(receivedTime * 1000)
      }
      if (!end) {
        return
      }
      this.countDownTimer = setInterval(() => {
        const start = moment(new Date())
        const diff = start.diff(end)
        const res = moment.duration(diff)
        this.countDownObj = {
          days: this.getNumber(res.days()),
          hours: this.getNumber(res.hours()),
          minutes: this.getNumber(res.minutes()),
          seconds: this.getNumber(res.seconds())
        }
      }, 1000)
    },

    getNumber(num) {
      num = Math.abs(num)
      if (num < 10) {
        return '0' + num
      }
      return num
    },

    // 获取用户钱包信息
    async getUserInfo() {
      const { accounts, preSaleABI } = this.$wallet
      if (accounts.length <= 0) {
        this.userBuyMax = 0
        this.userData = {}
        this.amount = 0
        this.isLogin = false
        this.isAllow = false
        return
      }

      this.userLoading = true
      this.userData = await preSaleABI.userInfo(accounts[0])
      this.isLogin = true
      const { amount, isAllow } = this.userData
      const { purchaseMax } = this.originData
      this.amount = utils.formatEther(amount)
      if (amount && purchaseMax) {
        this.userBuyMax = utils.formatEther(purchaseMax.sub(amount))
      }
      this.isAllow = isAllow
      this.userLoading = false
    },
    // 获取最大值
    async getMax() {
      await this.$wallet.connect()
      await this.getUserInfo()
      this.inputMax = this.userBuyMax
    },

    // 购买
    async buyHandler() {
      this.buyLoading = true
      try {
        let balance = await this.$wallet.preSaleABI.provider.getBalance(this.$wallet.accounts[0])
        balance = utils.formatEther(balance)
        if (balance < this.inputMax) {
          ElNotification({
            title: 'Purchase failed',
            message: 'Insufficient balance, please recharge first',
            type: 'error'
          })
          this.buyLoading = false
          return
        }
        const network = await this.$wallet.initNetwork()
        const tx = await network.preSaleABI.receivedETH({ value: utils.parseEther(this.inputMax) })
        await tx.wait()
        this.initData(false)
        this.getUserInfo()
        this.buyLoading = false
        ElNotification({
          title: 'Purchase successful',
          message: 'Congratulations on your successful purchase',
          type: 'success'
        })
      } catch (error) {
        this.buyLoading = false
        ElNotification({
          title: error.code,
          message: error.data ? error.data.message || error.message : error.message,
          type: 'error'
        })
      }
    }
  }
}
</script>

